import {useEffect,useState} from 'react'
import axios from 'axios'

function News(){
  const [response,setResponse] = useState("");
useEffect(()=>{

axios.get("https://freebetslips.onrender.com/api/read-news?public_key=s41744n253l27069u40w68246o583z51363f946n80678s462a21102r521j52656l42b51350w859p35746b991c59591c39p29173z713v63566j765d62804k990v78238e64w70253l76k53609l85x25324g149n31691e786u95736i405").then((response)=>{
  
  if(!response.status ==200){
    setResponse("")
  }else{
    if(response.data.status){
     setResponse(response.data.msg)
    }else{
      setResponse("")
    }
  }
},(err)=>{
  
  setResponse('')
})
},[])
if(response !==""){
  if(response.length>0){
 let data = response.map((news)=>

   <div className="news-con">
       <a href={news.source}>
         <img src={news.image_source}/>
         <div className="news-title">{news.title}</div>
         </a>
         </div>
   
 )
return(
  
<section className="sports-trending-news">
       <h1 className="display-6">
         Trending Sports News
       </h1>
       <div className="news-par d-flex">
        {data}
    
   
         </div>
     </section>
);
}else{
return(
    <div></div>
    );
}
}else{
  return(
    <div></div>
    );
}
}
export default News;