  import {useState,useEffect} from "react";
  import {NavLink} from 'react-router-dom';
  import moment from "moment"
  
  function closenav(){
  const  navbarclose = document.querySelector(".navbar-toggler");
  navbarclose.click()
  }
  function Nav(){
    
  const [dayafter,setDayAfter] = useState("");
  useEffect(()=>{
    let m = moment()
    setDayAfter(m.utc().add(2, 'day').endOf('day').format("dddd")+"'s")
  },[])
  return(
  <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <NavLink   onClick= {closenav}   className="nav-link" to={"/accumulators/today/all"}>Today's Accumulators
      
          </NavLink>
       
        </li>
        <li className="nav-item">
          <NavLink   onClick= {closenav}   className="nav-link" to={"/accumulators/tomorrow/all"}>Tomorrow's Accumulators</NavLink>
        </li>
         <li className="nav-item">
          <NavLink   onClick= {closenav}   className="nav-link" to={"/accumulators/"+dayafter.replace("'s","").toLowerCase()+"/all"}>{dayafter} Accumulators</NavLink>
        </li>
        <li className="nav-item">
          <NavLink   onClick= {closenav}   className="nav-link" to={"/aboutus"}>About us</NavLink>
        </li>
        <li className="nav-item">
          <NavLink   onClick= {closenav}   className="nav-link" to={"/contact"}>Contact</NavLink>
        </li>
        <li className="nav-item">
          <NavLink   onClick= {closenav}   className="nav-link" to={"/disclaimer"}>Disclaimer</NavLink>
        </li>
      </ul>
      );
  }
  export default Nav